<template>
<div class="sales-board">
  	<div class="sales-board-intro">
			<h2>小米手环</h2>
        	<p>看得见的时刻，和你的每一步
OLED 显示屏｜触摸操作｜全新腕带设计｜升级计步算法
149元
2017德国iF设计奖 小米手环 2
荣获国际工业设计大奖
2017德国iF设计奖</p>
		</div>
    <div class="sales-board-form">
			<div class="sales-board-line">
              <div class="sales-board-line-left">
                  购买数量：
              </div>
              <div class="sales-board-line-right">
                <Courent @counter="getGoodInfo('counter',$event)" :max="Counter.max" :min="Counter.min"/>
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
                  产品类型：
              </div>
              <div class="sales-board-line-right">
                    <DownMenu @downmenu="getGoodInfo('downmenu',$event)" :DownData="DownMenu"/>
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
                  颜色：
              </div>
              <div class="sales-board-line-right">
                <Radios @radios="getGoodInfo('radios',$event)" :radios='Radios'/>
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
                  总价：
              </div>
              <div class="sales-board-line-right">
                  {{totalprice}}元
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
              </div>
              <div class="sales-board-line-right">
                <button class="button">立即购买</button>
              </div>
            </div>
		</div>
    <div class="sales-board-des">
        <h2>时间、步数、心率
触手可及</h2>
        <p>全新的小米手环 2，加入了 OLED 显示屏，通过轻触圆形按键，即可显
示当前时间、步数、心率，甚至里程、热量等更多数据。当不方便触
控操作时，抬起手腕*，手环便可自动显示当前时间，轻松便捷。</p>

        <h3>全新计步算法
步步精准</h3>
        <ul>
          <li>与小米手环光感版相比，小米手环 2 基于全新的硬件平台升
级了运动计步算法</li>
          <li>有效的过滤了在无运动状态下的高
频率活动,</li>
          <li>大大提高计步的精准度,</li>
          <li>让每日的步数测量
数据更为准确</li>

        </ul>

        <h3>专属身份 ID </h3>
        <ul>
          <li>免密解锁更方便</li>
          <li>每个全新的小米手环 2 都是一个独立身份
ID,</li>
          <li>当使用安卓手机与小米手环靠近时，
即可通过手环免密解锁手机。</li>

        </ul>
      </div>
</div>
</template>
<script>
import Courent from "./components/counter";
import DownMenu from "./components/downmenu";
import Radios from "./components/radios";

export default {
  name: "car",
  data() {
    return {
      totalprice: 149,
      counter: 1,
      downmenu: 1,
      radios: 1,
      Counter: {
        max: 10,
        min: 1
      },
      DownMenu: [
        {
          name: "初级版",
          value: 1
        }
      ],
      Radios: [
        {
          name: "黑色",
          value: 1
        }
      ]
    };
  },
  components: {
    Courent,
    DownMenu,
    Radios
  },
  methods: {
    getGoodInfo(key, value) {
      /*
        this:当前组件
      */
      this[key] = value;
      var readyData = {
        counter: this.counter, // 5元
        downmenu: this.downmenu, // 2元
        radios: this.radios // 10元
      };
      this.totalprice =
        readyData.counter * 149 ;
    }
  }
  // computed:{
  //   getTotalPrice(){
  //     this.$store.dispatch("updatePrice", this.counter * 5 +  this.downmenu * 2 + this.radios * 10);
  //     return this.$store.getters.getTotalPrice;
  //   },
  // }
};
</script>
<style scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
}

.button {
  background: #4fc08d;
  color: #fff;
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
  border: 0;
  outline: none;
  border-radius: 5px;
}
</style>



