<template>
<div class="sales-board">
  	<div class="sales-board-intro">
			<h2>小米路由器 3
</h2>
        	<p>更快更强，不止四天线
四天线设计 / 128MB超大Flash / 802.11ac 快3倍 / 更安全更稳定</p>
		</div>
    <div class="sales-board-form">
			<div class="sales-board-line">
              <div class="sales-board-line-left">
                  购买数量：
              </div>
              <div class="sales-board-line-right">
                <Courent @counter="getGoodInfo('counter',$event)" :max="Counter.max" :min="Counter.min"/>
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
                  产品类型：
              </div>
              <div class="sales-board-line-right">
                    <DownMenu @downmenu="getGoodInfo('downmenu',$event)" :DownData="DownMenu"/>
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
                  颜色：
              </div>
              <div class="sales-board-line-right">
                <Radios @radios="getGoodInfo('radios',$event)" :radios='Radios'/>
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
                  总价：
              </div>
              <div class="sales-board-line-right">
                  {{totalprice}}元
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
              </div>
              <div class="sales-board-line-right">
                <button class="button">立即购买</button>
              </div>
            </div>
		</div>
    <div class="sales-board-des">
        <h2>一台信号更好的路由器</h2>
        <p>全新的四天线设计，802.11ac 千兆WiFi，128MB超大Flash配置，易用好玩的专属App。
你眼前的这台小米路由器3不仅信号更好、覆盖面积更大、稳定连接的设备数更多，我们还对之前深受用户好评的功能进行了优化。
做了很多，只为给你一台信号更好的智能路由器。</p>

        <h3>硬件配置</h3>
        <ul>
          <li>处理器  : MT7620A</li>
          <li>ROM:128MB SLC Nand Flash</li>
          <li>内存:128MB DDR2</li>
          <li>散热系统:自然散热</li>
          <li>硬盘存储:无</li>
        </ul>

        <h3>整机接口</h3>
        <ul>
          <li>1个USB 2.0接口（DC output：5V/1A）</li>
          <li>2个10/100M自适应LAN口（Auto MDI/MDIX）
</li>
          <li>1个10/100M自适应WAN口（Auto MDI/MDIX）
</li>
          <li>1个红/蓝/黄三色LED指示灯
</li>
          <li>1个系统恢复出厂设置按键
</li>
        </ul>
      </div>
</div>
</template>
<script>
import Courent from "./components/counter";
import DownMenu from "./components/downmenu";
import Radios from "./components/radios";

export default {
  name: "car",
  data() {
    return {
      totalprice:149,
      counter: 1,
      downmenu: 1,
      radios: 1,
      Counter: {
        max: 10,
        min: 1
      },
      DownMenu: [
        {
          name: "路由器",
          value: 1
        }
      ],
      Radios: [
        {
          name: "黑色",
          value: 1
        }
      ]
    };
  },
  components: {
    Courent,
    DownMenu,
    Radios
  },
  methods: {

        getGoodInfo(key,value){
      /*
        this:当前组件
      */
      this[key]=value;
      var readyData = {
        "counter":this.counter, // 5元
      };
      this.totalprice =  readyData.counter * 149 ;
    }
  },
  // computed:{
  //   getTotalPrice(){
  //     this.$store.dispatch("updatePrice", this.counter * 5 +  this.downmenu * 2 + this.radios * 10);
  //     return this.$store.getters.getTotalPrice;
  //   },
  // }
};
</script>
<style scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
}

.button {
  background: #4fc08d;
  color: #fff;
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
  border: 0;
  outline: none;
  border-radius: 5px;
}
</style>




