<template>
<div class="sales-board">
  	<div class="sales-board-intro">
			<h2>小米6 变焦双摄</h2>
        	<p>「限时特惠：4GB+64GB直降100元」变焦双摄，4 轴防抖 / 骁龙835 旗舰处理器，最高可选6GB 内存 / 5.15" 护眼屏</p>
		</div>
    <div class="sales-board-form">
			<div class="sales-board-line">
              <div class="sales-board-line-left">
                  购买数量：
              </div>
              <div class="sales-board-line-right">
                <Courent @counter="getGoodInfo('counter',$event)" :max="Counter.max" :min="Counter.min"/>
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
                  产品类型：
              </div>
              <div class="sales-board-line-right">
                    <DownMenu @downmenu="getGoodInfo('downmenu',$event)" :DownData="DownMenu"/>
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
                  颜色：
              </div>
              <div class="sales-board-line-right">
                <Radios @radios="getGoodInfo('radios',$event)" :radios='Radios'/>
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
                  总价：
              </div>
              <div class="sales-board-line-right">
                  {{totalprice}}元
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
              </div>
              <div class="sales-board-line-right">
                <button class="button">立即购买</button>
              </div>
            </div>
		</div>
    <div class="sales-board-des">
        <h2>七年工艺探索的梦幻之作</h2>
        <p>小米6 的美，来自于我们对精湛工艺不断的探索。梦幻般的四曲面玻璃机身，
璀璨夺目的四曲面陶瓷，熠熠生辉的不锈钢边框，不仅带来了流光溢彩的视觉体验，
更带来了整机无一丝棱线的绝佳手感。当光影在小米6 的机身上流淌，
犹如一颗宝石，珠圆玉润、璀璨生辉。</p>

        <h3>参数</h3>
        <ul>
          <li>骁龙835
高通旗舰处理器</li>
          <li>5.15" 护眼屏
有效降低蓝光辐射</li>
          <li>全网通4.0
双卡双待 / 4G+</li>
          <li>四曲面
高亮玻璃、陶瓷机身</li>
          <li>12MP+12MP
变焦双摄相机</li>
        </ul>

        <h3>陶瓷尊享版 6GB+128GB</h3>
        <ul>
          <li>骁龙835 最高主频 2.45GHz</li>
          <li>Adreno 540 图形处理器
</li>
          <li>最高主频 710MHz
</li>
          <li>6GB LPDDR4x 1866MHz 双通道 64位
</li>
          <li>128GB 机身存储 UFS</li>
        </ul>
      </div>
</div>
</template>
<script>
import Courent from "./components/counter";
import DownMenu from "./components/downmenu";
import Radios from "./components/radios";

export default {
  name: "car",
  data() {
    return {
      totalprice:2449,
      counter: 1,
      downmenu: 1,
      radios: 1,
      Counter: {
        max: 3,
        min: 1
      },
      DownMenu: [
        {
          name: "4GB+64GB",
          value: 1
        },
        {
          name: "6GB+64GB",
          value: 2
        },
        {
          name: "6GB+128GB",
          value: 3
        }
      ],
      Radios: [
        {
          name: "亮黑色",
          value: 1
        },
        {
          name: "亮白色",
          value: 2
        }
      ]
    };
  },
  components: {
    Courent,
    DownMenu,
    Radios
  },
  methods: {

        getGoodInfo(key,value){
      /*
        this:当前组件
      */
      this[key]=value;
      var readyData = {
        "counter":this.counter, // 5元
        "downmenu":this.downmenu, // 2元
        "radios":this.radios // 10元
      };
      this.totalprice =  readyData.counter * 2199 +  readyData.downmenu * 100 + readyData.radios * 150;
    }
  },
  // computed:{
  //   getTotalPrice(){
  //     this.$store.dispatch("updatePrice", this.counter * 5 +  this.downmenu * 2 + this.radios * 10);
  //     return this.$store.getters.getTotalPrice;
  //   },
  // }
};
</script>
<style scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
}

.button {
  background: #4fc08d;
  color: #fff;
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
  border: 0;
  outline: none;
  border-radius: 5px;
}
</style>




