<template>
  <div class="index-wrap">
      <div class="index-left">
        <div class="index-left-block">
          <h2>全部产品</h2>
          <template v-for="(product,index) in productList">
              <h3>{{product.category}}</h3>
              <ul>
                <li v-for="item in product.list">
                  <a target='_block':href="item.url">{{item.name}}</a>
                  <span v-if="item.hot" class="hot-tag">HOST</span>
                </li>
              </ul>
              <div v-if="index%2 == 0" class="hr"></div>
          </template>
        </div>
        <div class="index-left-block lastest-news">
          <h2>最新消息</h2>
          <ul>
            <li v-for="item in news">
              <a target="_block" :href="item.url">{{ item.name }}</a>
            </li>
          </ul>
        </div>
      </div>

  <div class="index-right">
  <div class="swiper-container">
    <swiper :options="swiperOption">
    <swiper-slide>
      <a target="_block" href="https://www.mi.com/mibookair-13/">
        <img class="swiperimg" src="../assets/images/1.jpg" alt="">
      </a>
    </swiper-slide>
    <swiper-slide>
        <a target="_block" href="https://www.mi.com/mibookair-13/">
        <img class="swiperimg" src="../assets/images/2.jpg" alt="">
      </a>
    </swiper-slide>
    <swiper-slide>
        <a target="_block" href="https://www.mi.com/mibookair-13/">
        <img class="swiperimg" src="../assets/images/3.jpg" alt="">
      </a>
      </swiper-slide>
    <swiper-slide>
        <a target="_block" href="https://www.mi.com/mibookair-13/">
        <img class="swiperimg" src="../assets/images/4.jpg" alt="">
      </a>
    </swiper-slide>
    <div class="swiper-pagination"  slot="pagination"></div>
    <div class="swiper-button-prev" slot="button-prev"></div>
    <div class="swiper-button-next" slot="button-next"></div>
  </swiper>
    </div>
    <div class="index-board-list">
      <div class="index-board-item" v-for='(board,index) in boardList' :class="['index-board-'+ board.tag ,{'line-last' : index%2 !==0}]">
        <div class="index-board-item-inner">
          <h2>{{board.title}}</h2>
          <p>{{board.desc}}</p>
          <div class="index-board-button">
              <router-link :to="{path:'/details/'+ board.tag}" class="button">立即购买</router-link>
          </div>
        </div>
      </div>
    </div>
      </div>
  </div>
</template>
<script>
export default {
  name: "layout",
  data() {
    return {
      swiperOption: {
        //swiper相关设置
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        autoplay: {
          delay: 5000,
          disableOnInteraction: true
        },
        loop: true,
        effect: "fade"
      },
      boardList: [
        {
          title: "小米MIX 2",
          desc: "全面屏2.0，5.99大屏",
          tag: "earth"
        },
        {
          title: "小米6 变焦双摄",
          desc: "变焦双摄，4 轴防抖 / 骁龙835 旗舰处理器",
          tag: "loud"
        },
        {
          title: "小米手环",
          desc: "看得见的时刻，和你的每一步",
          tag: "car"
        },
        {
          title: "小米路由器 3",
          desc: "更快更强，不止四天线/四天线设计",
          tag: "hill"
        }
      ],
      productList: [
        {
          category: "小米系列",
          list: [
            {
              name: "小米MIX 2",
              url: "https://item.mi.com/product/10000069.html?cfrom=search",
              hot: false
            },
            {
              name: "小米手环",
              url: "https://www.mi.com/mix2/",
              hot: false
            },
            {
              name: "小米路由器 3",
              url: "https://www.mi.com/mi6/",
              hot: true
            }
          ]
        },
        {
          category: "小米盒子系列",
          list: [
            {
              name: "小米盒子4",
              url: "https://www.mi.com/mibox4/",
              hot: false
            },
            {
              name: "小米盒子4c",
              url: "https://www.mi.com/mibox4c/",
              hot: true
            },
            {
              name: "小米盒子3s",
              url: "https://www.mi.com/mibox3s/",
              hot: false
            }
          ]
        }
      ],
      news: [
        {
          name: "小米MIX 2S新品发布会",
          url: "http://bbs.xiaomi.cn/t-14475729"
        },
        {
          name: "小米超神",
          url: "http://wan.xiaomi.cn/activity/detail/activity_id/151"
        }
      ]
    };
  }
};
</script>
<style scoped>
.index-wrap {
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}
.index-left {
  float: left;
  width: 300px;
  text-align: left;
}
.index-right {
  float: left;
  width: 900px;
}
.index-left-block {
  margin: 15px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
  height: 350px;
}
.index-left-block .hr {
  margin-bottom: 20px;
  height: 1px;
  width: 100%;
  background: #ddd;
}
.index-left-block h2 {
  background: #4fc08d;
  color: #fff;
  padding: 10px 15px;
  margin-bottom: 20px;
}
.index-left-block h3 {
  padding: 0 15px 5px 15px;
  font-weight: blod;
  color: #222;
}
.index-left-block ul {
  padding: 10px 15px;
}
.index-left-block li {
  padding: 5px;
}
.lastest-news {
  min-height: 350px;
}
.hot-tag {
  background: red;
  color: #fff;
}
.swiperimg {
  width: 900px;
}
.swiper-container {
  margin: 10px 0;
  width: 900px;
}
.index-borad-list {
  overflow: hidden;
  margin-top: 15px;
}
.index-board-item {
  float: left;
  width: 400px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
  padding: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
}
.index-board-item-inner {
  min-height: 125px;
  padding-left: 120px;
}
.index-board-car .index-board-item-inner {
  background: url(../assets/images/1.png) no-repeat;
}
.index-board-loud .index-board-item-inner {
  background: url(../assets/images/2.png) no-repeat;
}
.index-board-earth .index-board-item-inner {
  background: url(../assets/images/3.png) no-repeat;
}
.index-board-hill .index-board-item-inner {
  background: url(../assets/images/4.png) no-repeat;
}
.index-board-item h2 {
  font-size: 18px;
  font-weight: bold;
  color: #000;
  margin-bottom: 15px;
}

.line-last {
  margin-right: 0;
}
.index-board-button {
  margin-top: 20px;
}
.lastest-news {
  min-height: 350px;
}
.button {
  background: #4fc08d;
  color: #fff;
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
  border: 0;
  outline: none;
  border-radius: 5px;
}
.button:hover {
  background: #4fc;
  color: #000;
}
</style>



