<template>
<div class="sales-board">
  	<div class="sales-board-intro">
			<h2>小米MIX 2 全面屏2.0</h2>
        	<p>「8GB+128GB直降400元，6GB+128GB直降200元，6GB+64GB直降200元」全面屏2.0，5.99"大屏 / 骁龙835旗舰处理器 / 4轴光学防抖 / 6模43频，全球频段。</p>
		</div>
    <div class="sales-board-form">
			<div class="sales-board-line">
              <div class="sales-board-line-left">
                  购买数量：
              </div>
              <div class="sales-board-line-right">
                <Courent @counter="getGoodInfo('counter',$event)" :max="Counter.max" :min="Counter.min"/>
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
                  产品类型：
              </div>
              <div class="sales-board-line-right">
                    <DownMenu @downmenu="getGoodInfo('downmenu',$event)" :DownData="DownMenu"/>
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
                  颜色：
              </div>
              <div class="sales-board-line-right">
                <Radios @radios="getGoodInfo('radios',$event)" :radios='Radios'/>
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
                  总价：
              </div>
              <div class="sales-board-line-right">
                  {{totalprice}}元
              </div>
            </div>

            <div class="sales-board-line">
              <div class="sales-board-line-left">
              </div>
              <div class="sales-board-line-right">
                <button class="button">立即购买</button>
              </div>
            </div>
		</div>
    <div class="sales-board-des">
        <h3>小米MIX 2 黑色陶瓷版</h3>
        <ul>
          <li>5.99英寸 全面屏</li>
          <li>骁龙835 最高主频 2.45GHz</li>
          <li>Adreno 540 图形处理器
</li>
          <li>64GB/128GB/256GB 机身存储
</li>
          <li>闪存存储标准 UFS 2.1
</li>
        </ul>

        <h3>小米MIX 2 全陶瓷尊享版  /  斯塔克限量版</h3>
        <ul>
          <li>外观：Unibody全陶瓷</li>
          <li>高度：150.5 mm
</li>
          <li>高度：150.5 mm
</li>
          <li>宽度：74.6 mm
</li>
          <li>厚度：7.7 mm
</li>
        </ul>
      </div>
</div>
</template>
<script>
import Courent from "./components/counter";
import DownMenu from "./components/downmenu";
import Radios from "./components/radios";

export default {
  name: "car",
  data() {
    return {
      totalprice:3399,
      counter: 1,
      downmenu: 1,
      radios: 1,
      Counter: {
        max: 5,
        min: 1
      },
      DownMenu: [
        {
          name: "6GB+64GB",
          value: 1
        },
        {
          name: "6GB+128GB",
          value: 2
        },
        {
          name: "Unibody 全陶瓷",
          value: 3
        }
      ],
      Radios: [
        {
          name: "黑色陶瓷",
          value: 1
        },
        {
          name: "红色",
          value: 2
        }
      ]
    };
  },
  components: {
    Courent,
    DownMenu,
    Radios
  },
  methods: {

        getGoodInfo(key,value){
      /*
        this:当前组件
      */
      this[key]=value;
      var readyData = {
        "counter":this.counter, // 5元
        "downmenu":this.downmenu, // 2元
        "radios":this.radios // 10元
      };
      this.totalprice =  readyData.counter * 3099 +  readyData.downmenu * 200 + readyData.radios * 100;
    }
  },
  // computed:{
  //   getTotalPrice(){
  //     this.$store.dispatch("updatePrice", this.counter * 5 +  this.downmenu * 2 + this.radios * 10);
  //     return this.$store.getters.getTotalPrice;
  //   },
  // }
};
</script>
<style scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
}

.button {
  background: #4fc08d;
  color: #fff;
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
  border: 0;
  outline: none;
  border-radius: 5px;
}
</style>



