<template>
<div class='counter-wrap'>
    <div class='counter-btn' @click="mins">
       -
    </div>
    <div class="counter-input">
      <input type="text" v-model="number" @keyup="inputHandler">
    </div>
    <div class="counter-btn" @click="maxs">
      +
    </div>
</div>
</template>
<script>
export default {
  name: "counter",
  data() {
    return {
      number: 1
    };
  },
  //从父级接受数据
  props: {
    min: {
      type: Number,
      default: 1
    },
    max: {
      type: Number,
      default: 5
    }
  },
  methods: {
    mins() {
      if (this.number <= this.min) {
        return;
      }
      this.number--;
      // 往父级传递数据
      this.$emit("counter", this.number);
    },
    maxs() {
      if (this.number >= this.max) {
        return;
      }
      this.number++;
      this.$emit("counter", this.number);
    },
    inputHandler() {
      let fix;
      if (typeof this.number === "string") {
        //正则表达式 \D 非数字
        fix = Number(this.number.replace(/\D/g, ""));
      } else {
        fix = this.number;
      }
      if (fix > this.max || fix < this.min) {
        // 把最小值给他
        fix = this.min;
      }
      this.number = fix;
      this.$emit("counter", this.number);
    }
  }
};
</script>
<style scoped>
div {
  user-select: none;
}
.counter-wrap {
  position: relative;
  display: inline-block;
  overflow: hidden;
  vertical-align: middle;
}
.counter-input {
  float: left;
}
.counter-input input {
  border: none;
  border-top: 1px solid #e3e3e3;
  border-bottom: 1px solid #e3e3e3;
  height: 23px;
  line-height: 23px;
  width: 30px;
  outline: none;
  text-indent: 4px;
  text-align: center;
}
.counter-btn {
  border: 1px solid #e3e3e3;
  float: left;
  height: 25px;
  line-height: 25px;
  width: 25px;
  text-align: center;
  cursor: pointer;
}
.counter-btn:hover {
  border-color: #4fc08d;
  background: #4fc08d;
  color: #fff;
}
</style>



