<template>
<div class="radios-wrap">
  <ul class="radios-lost">
	    <li @click="choose(index)" v-for="(item,index) in radios":class="{'active':index == nowIndex}">{{ item.name }}</li>
  </ul>
  </ul>
</div>
</template>
<script>
export default {
  name: "radios",
  data() {
    return {
      nowIndex: 0
    };
  },
  methods: {
    choose(index) {
      this.nowIndex = index;
      this.$emit('radios',this.radios[index].value)
    }
  },
  props: {
    radios: {
      type: Array,
      default: function() {
        return [
          {
            name: "mas",
            value: 1
          }
        ];
      }
    }
  }
};
</script>
<style scoped>
.radios-wrapt {
  position: relative;
  display: inline-block;
}
.radios-lost li {
  display: inline-block;
  border: 1px solid #e3e3e3;
  height: 25px;
  line-height: 25px;
  padding: 0 8px;
  margin-right: 5px;
  border-radius: 3px;
  text-align: center;
  cursor: pointer;
}
.radios-lost li.active {
  border-color: #4fc08d;
  background: #4fc08d;
  color: #fff;
}
</style>



